import React, { Component } from 'react';
import styles from './Login.module.scss';
import { IoMdMail, IoIosLock, IoIosLogIn } from 'react-icons/io';
import { TextField, Typography, Divider, Container, Button } from '@material-ui/core';

class Login extends Component {
    state = {
        email: '',
        password: ''
    }

    handleChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    login = () => {
        alert('login!');
    }

    render() {
        return (
            <Container maxWidth='sm' className={styles.loginWrapper}>
                <div className={styles.loginTitle}>
                    <IoIosLogIn className={styles.loginTitleIcon} />

                    <Typography variant='h6'>
                        Ingresa tus credenciales
                    </Typography>
                </div>

                <Divider style={{ margin: '1rem 0' }} />

                <div className={styles.inputBlock}>
                    <IoMdMail className={styles.inputBlockIcon} />

                    <TextField
                        fullWidth
                        label='Ingresa tu correo electrónico'
                        onChange={event => this.handleChange(event)}
                        name='email'
                    />
                </div>

                <div className={styles.inputBlock}>
                    <IoIosLock className={styles.inputBlockIcon} />

                    <TextField
                        fullWidth
                        label='Ingresa tu contraseña'
                        type='password'
                        onChange={event => this.handleChange(event)}
                        name='password'
                    />
                </div>

                <Button
                    fullWidth
                    className={styles.loginButton}
                    onClick={this.login}
                >
                    Ingresar
                </Button>
            </Container>
        );
    }
}

export default Login;