import React from 'react';
import styles from './UserHeader.module.scss';
import StarRatings from 'react-star-ratings';
import cssColors from './../../../scss/_colors.scss';
import { Typography } from '@material-ui/core';

const UserHeader = ({ rating, changeRating }) => {
    return (
        <div className={styles.userHeader}>
            <div className={styles.userImage}>
            </div>
            <div className={styles.userHeaderTitle}>
                <div>
                    <Typography variant='h6' color='textSecondary'>
                        Perfíl de:
                            </Typography>
                    <Typography variant='h2' className={styles.userName}>
                        Danielpik18
                    </Typography>
                </div>
            </div>
            <div className={styles.userRating}>
                <StarRatings
                    rating={rating}
                    changeRating={changeRating}
                    numberOfStars={5}
                    name='rating'
                    starDimension='1.4rem'
                    starSpacing='0px'
                    starRatedColor={cssColors.yellowLight}
                    starEmptyColor={cssColors.greyLight}
                    starHoverColor={cssColors.redLight}
                />
            </div>
        </div>
    );
}

export default UserHeader;