import React, { Component } from 'react';
import styles from './Register.module.scss';
import {
    Typography,
    TextField,
    Divider,
    Select,
    Input,
    Button
} from '@material-ui/core';
import { IoMdMail, IoIosLock, IoIosPerson } from 'react-icons/io';
import { TiEdit } from 'react-icons/ti';

import * as EmailValidator from 'email-validator';
import uniqid from 'uniqid';

import { base } from './../../firebase';

class Register extends Component {
    state = {
        email: '',
        password: '',
        role: ''
    }

    renderValue = (value) => {
        return value;
    }

    getRandomId

    submitUser = () => {
        if (EmailValidator.validate(this.state.email)) {
            base.post(`users/${uniqid()}`, { data: { ...this.state } });
            return alert('User added correctly');
        }
        else {
            return alert('Email incorrect');
        }
    }

    handleChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    render() {

        return (
            <div>
                <div className={styles.wrapper}>
                    <div className={styles.bgImage}></div>

                    <div className={styles.registerBox}>
                        <div className={styles.title}>
                            <TiEdit className={styles.titleIcon} />
                            <Typography variant='h6' color='textSecondary'>
                                {'Registrate'.toUpperCase()}
                            </Typography>
                        </div>

                        <Divider style={{ margin: '1rem 0' }} />

                        <div className={styles.inputBlock}>
                            <IoMdMail className={styles.inputBlockIcon} />

                            <TextField
                                fullWidth
                                label='Ingresa tu correo electrónico'
                                onChange={event => this.handleChange(event)}
                                name='email'
                            />
                        </div>

                        <div className={styles.inputBlock}>
                            <IoIosLock className={styles.inputBlockIcon} />

                            <TextField
                                fullWidth
                                label='Ingresa tu contraseña'
                                type='password'
                                onChange={event => this.handleChange(event)}
                                name='password'
                            />
                        </div>

                        <div className={styles.inputBlock}>
                            <IoIosPerson className={styles.inputBlockIcon} />

                            <Select
                                native
                                onChange={event => this.handleChange(event)}
                                name='role'
                                value={this.state.role}
                                fullWidth
                                displayEmpty
                                input={<Input name="rol" id="rolUsuario" />}
                            >
                                <option value="">
                                    Selecciona un rol
                                </option>
                                <option value={'Desarrollador'}>Desarrollador</option>
                                <option value={'Reclutador'}>Reclutador</option>
                            </Select>
                        </div>

                        <Button
                            fullWidth
                            className={styles.submitButton}
                            onClick={this.submitUser}
                        >
                            Completar registro
                        </Button>
                    </div>
                </div>
            </div>
        );
    }
}

export default Register;