import React from 'react';
import {
    Drawer,
    List,
    ListItem,
    ListItemIcon,
    Dialog
} from '@material-ui/core';

import {
    IoIosPerson,
    IoIosLogIn
} from 'react-icons/io';

import { Link } from 'react-router-dom';

import styles from './SideDrawer.module.scss';

const SideDrawer = ({ isOpen, toggle }) => {
    const menuOptions = [
        {
            title: 'Mi perfil',
            icon: <IoIosPerson />,
            path: `/profile11`,
        },
        {
            title: 'Ingresar',
            icon: <IoIosLogIn />,
            path: 'login'
        }
        /*
        ,
        {
            title: 'Buscar desarroladores',
            icon: <IoIosSearch />
        },
        */
    ];

    return (
        <Drawer
            open={isOpen}
            onClose={toggle}
        >
            <List className={styles.drawerMenu}>
                {
                    menuOptions.map(option => (
                        <Link
                            to={option.path}
                            key={option.title}
                            style={{
                                textDecoration: 'none',
                                width: '100%',
                                textAlign: 'center'
                            }}
                            onClick={toggle}

                        >
                            <ListItem button >
                                <ListItemIcon className={styles.drawerMenuIcon}>
                                    {option.icon}
                                </ListItemIcon>
                                <span className={styles.drawerMenuText}>{option.title}</span>
                            </ListItem>
                        </Link>
                    ))
                }
            </List>
        </Drawer>
    );
}

export default SideDrawer;